@charset "UTF-8";
@font-face { font-family: "rob_l"; src: url("../fonts/RobotoCondensed-Light.ttf") format("truetype"), url("../fonts/RobotoCondensed-Light.svg#rob_l") format("svg"); src: url("../fonts/RobotoCondensed-Light/RobotoCondensed-Light.woff") format("woff"); font-style: normal; font-weight: normal; }

@font-face { font-family: "rob_r"; src: url("../fonts/RobotoCondensed-Regular.ttf") format("truetype"), url("../fonts/RobotoCondensed-Regular.svg#rob_r") format("svg"); src: url("../fonts/RobotoCondensed-Regular/RobotoCondensed-Regular.woff") format("woff"); font-style: normal; font-weight: normal; }

@font-face { font-family: "rob_b"; src: url("../fonts/RobotoCondensed-Bold.ttf") format("truetype"), url("../fonts/RobotoCondensed-Bold.svg#rob_b") format("svg"); src: url("../fonts/RobotoCondensed-Bold/RobotoCondensed-Bold.woff") format("woff"); font-style: normal; font-weight: normal; }

body { padding: 0; margin: 0; font-family: 'Open Sans'; font-size: 20px; color: #fff; font-weight: 400; }

body p { margin: 0 0 25px 0; line-height: 2em; }

body a { text-decoration: none; color: inherit; transition: .3s; }

body a:hover { text-decoration: none; cursor: pointer; }

body ul { margin: 0; padding: 0; }

body ul li { list-style: none; }

body ol li { line-height: 22px; }

body b, body strong { font-family: 'Open Sans'; font-weight: 700; }

body * { box-sizing: border-box; }

body img { max-width: 100%; height: auto; }

body .container { max-width: 1200px; margin: 0 auto; }

body .field-item { line-height: 2em; }

body h1 { margin: 0; }

body h2 { font-size: 1.8em; }

body .contextual-region { position: relative; }

body #block-asterics-local-tasks { position: fixed; z-index: 999; top: 400px; right: 0; }

body #block-asterics-local-tasks ul { margin: 0; text-align: right; border: none; padding: 0; }

body #block-asterics-local-tasks ul li { display: block; margin: 5px 0; }

body #block-asterics-local-tasks ul li a { margin: 0; padding: 10px 20px; display: inline-block; background: rgba(30, 30, 30, 0.85); color: #fff; border: none; font-weight: 300; box-shadow: 0 0 60px rgba(0, 0, 0, 0.3); transition: .3s; -webkit-transition: .3s; }

body #block-asterics-local-tasks ul li a.active { background: #fff; color: #333; }

body #block-asterics-local-tasks ul li a:hover { padding-right: 50px; background: #fff; color: #333; }

body .regular-20, body .paragraph--view-mode--regular-20 { font-size: 20px; }

body .regular-24, body .paragraph--view-mode--regular-24 { font-size: 24px; }

body .extrabold-40, body .paragraph--view-mode--extrabold-40 { font-size: 40px; font-weight: 900; letter-spacing: 1.5px; }

body .paragraph--type--link { margin: 40px 0; }

body .paragraph--type--link a { display: inline-block; width: auto; padding: 20px 30px; border-radius: 30px; color: #fff; font-weight: 300; font-size: 14px; text-transform: uppercase; background-color: #f2644c; }

/* MAPS */
.geolocation-common-map { /*&:before { @include defaultBackground(); }*/ }

.geolocation-common-map-locations { /*display:block !important;*/ position: fixed; top: 0; left: 0; z-index: 1; background: rgba(0, 0, 0, 0.7); width: 100%; height: 100%; padding: 60px; }

.geolocation-common-map-locations .geolocation { display: none; }

.geolocation-common-map-locations .geolocation .hidden { display: none; }

.geolocation-common-map-locations .geolocation .location-content { overflow: hidden; border: none; }

.gm-style-iw-d { color: #000; }

/* TAXONOMÍA */
.taxonomy.observatory.full { padding: 60px; }

.taxonomy.observatory.full h2 { font-size: 40px; line-height: normal; }

.taxonomy.observatory.full p { line-height: normal; margin: 5px 0; }

.taxonomy.observatory.full .cols { display: flex; }

.taxonomy.observatory.full .cols .col { flex: 1 0 50%; display: flex; justify-content: center; flex-direction: column; }

.taxonomy.observatory.full .cols .col:nth-child(1) { padding-right: 20px; }

.taxonomy.observatory.full .cols .col:nth-last-child(1) { padding-left: 20px; align-items: flex-end; }

.taxonomy.observatory.full .cols .field_observatory_website a { font-size: 13px; }

.taxonomy.observatory.full .cols .field_observatory_website a:hover { color: #f2644c; }

.taxonomy.observatory.full .box { width: 100%; display: inline-block; display: flex; }

.taxonomy.observatory.full .box.notform .address, .taxonomy.observatory.full .box.notform .views-element-container { flex: 1 0 33%; }

.taxonomy.observatory.full .box.notform .field_observatory_observacio { display: none; }

.taxonomy.observatory.full .address { padding: 20px; flex: 1 0 25%; margin-top: 80px; color: black; background-color: white; }

.taxonomy.observatory.full .address h3 { font-size: 20px; margin: 5px 0 20px; }

.taxonomy.observatory.full .address > p { font-size: 16px; margin: 5px 0; }

.taxonomy.observatory.full .address .field_observatory_phone { font-weight: bold; font-size: 18px; margin: 5px 0; }

.taxonomy.observatory.full .address .field_observatory_website { color: #f2644c; font-size: 18px; margin: 5px 0; }

.taxonomy.observatory.full .address .field_observatory_address { margin: 15px 0; }

.taxonomy.observatory.full .views-element-container { flex: 1 0 25%; padding: 20px; background-color: #53d6f9; color: white; }

.taxonomy.observatory.full .views-element-container:nth-last-child(1) { background-color: #f2644c; margin-top: 60px; }

.taxonomy.observatory.full .views-element-container header { text-align: left; }

.taxonomy.observatory.full .views-element-container footer a { width: auto; padding: 10px 25px; border-radius: 20px; background-color: #f2644c; color: white; text-transform: uppercase; font-family: 'rob_l'; display: inline-block; margin-top: 40px; }

.taxonomy.observatory.full .view-observation-list .views-row { margin-bottom: 15px; padding-bottom: 15px; border-bottom: 1px solid white; }

.taxonomy.observatory.full .view-observation-list .views-field { display: inline-block; vertical-align: middle; max-width: 50%; text-align: left; font-size: 16px; }

.taxonomy.observatory.full .view-observation-list .ra { padding-right: 5px; }

.taxonomy.observatory.full .view-observation-list .dec { padding-left: 5px; }

.taxonomy.observatory.full .view-observation-list .views-field-completed { width: 100%; display: inline-block; max-width: none; }

.taxonomy.observatory.full .view-id-news.view-display-id-entity_view_2 { opacity: 0; transition: .5s; }

.taxonomy.observatory.full .field_observatory_observacio { background-color: #6a7791; color: white; padding: 20px; flex: 1 0 25%; margin-top: 80px; }

.taxonomy.observatory.full .field_observatory_observacio input, .taxonomy.observatory.full .field_observatory_observacio textarea { background-color: #c3c9d3; color: white; }

.taxonomy.observatory.full .field_observatory_observacio input[type="submit"] { background-color: #f2644c; }

.taxonomy.observatory.full .field_observatory_observacio .js-form-type-datetime, .taxonomy.observatory.full .field_observatory_observacio .form-item-observatory { display: none !important; }

/* VISTAS */
.view.view_obervatories-map.view_display_map .view-header { text-align: center; margin-bottom: 60px; }

.view.view_obervatories-map.view_display_map .view-header h4 { display: inline-block; width: 100%; font-size: 20px; font-weight: 400; margin: 0 0 20px; }

.view.view_obervatories-map.view_display_map .view-header h1 { font-size: 40px; font-weight: 700; margin: 20px 0 60px; }

.view.view_obervatories-map.view_display_map .view-header a { border-radius: 20px; color: white; padding: 10px 25px 10px 50px; display: inline-block; vertical-align: middle; opacity: 1; }

.view.view_obervatories-map.view_display_map .view-header a:nth-child(3) { margin-right: 40px; background: #f2644c url("../images/antena.svg") no-repeat 5px center; }

.view.view_obervatories-map.view_display_map .view-header a:nth-child(4) { background: #f2644c url("../images/espiral.svg") no-repeat 10px center; padding-left: 55px; }

.view.view_obervatories-map.view_display_map .view-header a:hover { background-color: #53d6f9; }

.node.new.visualizacion-1 { text-align: left; }

.node.new.visualizacion-1 .field_media_image { text-align: center; }

.node.new.visualizacion-1 h2 { font-size: 20px; font-weight: bold; }

/* USER */
#forms-wrapper { position: absolute; top: 0; z-index: 99; background: rgba(0, 0, 0, 0.5); width: 100%; height: 100%; display: none; }

#observation-form-wrapper { position: absolute; z-index: 1; display: none; }

/* FORMS */
form .form-item { margin: 5px 0; width: 100%; display: inline-block; }

form .form-actions { text-align: center; margin-top: 30px; }

form input, form textarea { appearance: none; border: 0px; padding: 7px 10px; width: 100%; font-size: 16px; }

form input[type="submit"] { width: auto; padding: 10px 25px; border-radius: 20px; background-color: #f2644c; color: white; text-transform: uppercase; font-family: 'rob_l'; }

form label { font-size: 18px; font-weight: bold; }
